.hero-container{
    background: url('../images/img-1.jpg') center center/cover no-repeat;
    height: 100vh;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    box-shadow: inset 0 0 0 1000px rgba(0,0,0,0.2);
    object-fit: contain;
    justify-content: center;
}

.hero-container > h1  {
    color: #fff;
    font-size:100px;
    margin-top:-100px;
}

.hero-container > p{
    margin-top: 8px;
    color: #fff;
    font-family: 'Trebuchet MS', 'Lucida Sans', 'Lucida Grande', 
    'Lucida Sans Unicode', Arial, sans-serif;
}


@media screen and (max-width: 960px){
    .hero-container > h1  {
        font-size: 70px;
        margin-top:-150px;
    }
}

@media screen and (max-width: 768px){
    .hero-container > h1  {
        font-size:50px;
        margin-top:-100px;
    }

    .hero-container > p{
        font-size: 30px;
    }
}