.footer-container{
    background-color: #242424;
    padding: 0.05rem 0 2rem 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}


.footer-links {
    width: 100%;
    max-width: 1000px;
    display: flex;
    justify-content: center;
  }

.footer-link-wrapper{
    display: flex;
}

.footer-link-items {
    display: flex;
    flex-direction: column;
    margin: 16px;
    text-align: center;
    width: 220px;
    box-sizing: border-box;
    color: white;
  }
  
  .footer-link-items h2 {
    margin-bottom: 16px;
  }

  .footer-link-items > h2 {
    color: #fff;
    /* text-indent: 500px; */
  }
  
  .footer-link-items a {
    color: #fff;
    text-decoration: none;
    margin-bottom: 8px;
    font-size: 16px;
  }
  
  .footer-link-items a:hover {
    color: #7faacf;
    transition: 0.3s ease-out;
  }
  
  .footer-email-form h2 {
    margin-bottom: 2rem;
  }
  
  .footer-input::placeholder {
    color: #b1b1b1;
  }
  

  /* Social Icons */
  .social-icon-link {
    color: #fff;
    /* font-size: 24px; */
  }
  
  .social-media {
    max-width: 10000px;
    width: 100%;
    margin-top: -25px;
    margin-bottom: -15px;
  }
  
  .social-media-wrap {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    max-width: 1000px;
    margin: 40px auto 0 auto;
  }
  
  .social-icons > a {
    display:inline-block;
    justify-content: space-between;
    align-items: left;
    width: 80px;
    font-size: 2rem;
  }

  .social-icons a:hover {
    color: #7faacf;
    transition: 0.3s ease-out;
  }
  
  
  .website-rights {
    color: #fff;
    margin-bottom: 2px;
  }
  
  @media screen and (max-width: 820px) {
    .footer-links {
      padding-top: 2rem;
    }
  
    .footer-input {
      width: 100%;
    }
  
    .btn {
      width: 100%;
    }
  
    .footer-link-wrapper {
      flex-direction: column;
    }
  
    .social-media-wrap {
      flex-direction: column;
    }
    .footer-link-items {
        width: 195px;
      }
  }
  
  @media screen and (max-width: 768px) {
    .footer-link-items {
        width: 175px;
      }
  }

